/*! Defaults */
body {
  color: $main-color;
  font-family: $default-font;
  @include font-size(16px); }


p::selection {
    color: $white;
    background: $accentcolor;
    padding: 5px; }
h1::selection {
    color: $white;
    background: $accentcolor;
    padding: 5px; }
h2::selection {
    color: $white;
    background: $accentcolor;
    padding: 5px; }
h3::selection {
    color: $white;
    background: $accentcolor;
    padding: 5px; }
h4::selection {
    color: $white;
    background: $accentcolor;
    padding: 5px; }
span::selection {
    color: $white;
    background: $accentcolor;
    padding: 5px; }
a::selection {
    color: $white;
    background: $accentcolor;
    padding: 5px; }
div::selection {
    color: $white;
    background: $accentcolor;
    padding: 5px; }

// sticky footer (always at the bottom of page)

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%; }

.content {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

