/*! .header */
.header {
  transition: all linear .5s;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 23px;
  letter-spacing: 0.02em;
  font-family: Museo;
  height: 118px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 999;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), rgba(255, 255, 255, 0.3);
}

.header__container {
  margin: 0 106px;
  transition: all linear .5s;
  padding: 16px 0;
}

.header__content {
  transition: all linear .5s;
  display: flex;
  align-items: center;
}

.header__content a {
  text-decoration: none;
}

.header__content-logo {
  display: flex;
  align-items: center;
  width: 45%;
  transition: all linear .5s;
}

.header__content-logo p {
  padding: 0 20px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 23px;
  letter-spacing: 0.02em;
}

.header__content-phones ul {
  display: flex;
}

.header__content-phones ul li a {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.02em;
  color: #F4F4F4;
  text-decoration: none;
}

.header__content-callback {
  display: flex;
  align-items: center;
}

.btn-row {
  position: absolute;
  right: 29px;
  margin-right: 40px;
}

.btn-row:last-child {
  margin: 0;
}

.js-burger {
  border-radius: 2px !important;
}

.burger {
  position: relative;
  display: block;
  width: 34px;
  height: 30px;
  padding: 13px 0;
  border-radius: 2px;
  background-color: #fff;
  background-clip: content-box;
  cursor: pointer;
  transition: background 50ms ease-in 250ms;
}

.burger:before, .burger:after {
  content: '';
  border-radius: 2px;
  position: absolute;
  width: 100%;
  height: 4px;
  top: 0;
  left: 0;
  background: #fff;
  transform: rotate(0deg) scale(1);
  transition: top 0.25s ease-in 0.3s, transform 0.25s ease-in 0s;
}

.burger:after {
  top: 26px;
}

.burger.is-open {
  background-color: transparent;
  transition: background 50ms ease-in 250ms;
}

.burger.is-open:before, .burger.is-open:after {
  top: 13px;
  transition: top 0.25s ease-in 0s, transform 0.25s ease-in 0.3s;
}

.burger.is-open:before {
  transform: rotate(-45deg) scaleX(1.2);
}

